<template>
  <div class="infomation-wrap">
    <template v-if="messageList.length > 0">
      <InformationItem
        v-for="item in messageList"
        :key="item.id"
        @show="handlePop"
        :dataSource="item"
      ></InformationItem>
    </template>
    <div class="no-data" v-else>{{ $t('common.noData') }}</div>
    <InformationDetailPop
      v-if="detailShow"
      :dataSource="tempData"
      @close="handlePop"
    ></InformationDetailPop>
  </div>
</template>

<script>
import { handleMessageList } from '@/services/information'
import InformationItem from './components/InformationItem.vue'
import InformationDetailPop from './components/InformationDetailPop.vue'
export default {
  name: '',
  data () {
    return {
      detailShow: false,
      messageList: [],
      tempData: {}
    }
  },
  methods: {
    handlePop (temp) {
      this.tempData = temp
      this.detailShow = !this.detailShow
    },
    async getList () {
      const resp = await handleMessageList()
      if (resp.code !== 200) return
      this.messageList = resp.rows
    }
  },
  mounted () {
    this.getList()
  },
  components: { InformationItem, InformationDetailPop }
}
</script>

<style scoped lang="scss">
.infomation-wrap {
  color: #dbd9d5;
  font-size: 12px;
  padding: 15px;
}
</style>
